@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
@font-face {
  font-family: "Sharp Sans";
  src: url("../assets/fonts/SharpSansNo1-Semibold.woff") format("woff");
}
:root {
  --font-title: "Sharp Sans", sans-serif;
  --font-body: "DM Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(230, 41%, 14%);
  --green: hsl(167, 100%, 45%);
  --cream: hsl(29, 80%, 92%);
}

/* Removing margins and padding. Box sizing rules */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul,
ol {
  list-style-type: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  color: var(--blue);
}

/* Removing default text decoration on anchor tag */
a {
  text-decoration: none;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Setting body fonts */
html {
  font-family: var(--font-body);
}

/* Setting title fonts */
h1,
h2,
h3 {
  font-family: var(--font-title);
}

.title--primary {
  font-size: 2.8125rem;
  line-height: 3.4375rem;
  font-weight: 600;
}
@media (max-width: 767px) {
