    gap: 0.5rem;
    justify-self: end;
  }
}

.line {
  height: 2px;
  width: 30px;
  background: var(--green);
}
.line:nth-child(2) {
  position: relative;
  overflow: visible;
}
.line:nth-child(2)::after, .line:nth-child(2)::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  inset: 0;
  background: var(--green);
  transition: transform 200ms ease-in-out;
}

.hamburger1 .line {
  background: transparent;
}
.hamburger1 .line:nth-child(2)::after {
  transition: transform 200ms ease-in-out;
  transform: rotate(45deg);
}
.hamburger1 .line:nth-child(2)::before {
  transition: transform 200ms ease-in-out;
  transform: rotate(-45deg);
}

